/*===Width===*/
.w-100 {
  width: 100%;
}

@media (min-width: 768px) {
  .w-lg-100 {
    width: 100%;
  }
}

.w-60 {
  width: 60%;
}

@media (min-width: 992px) {
  .w-lg-60 {
    width: 60%;
  }
}

.w-40 {
  width: 40%;
}

/*===Colors===*/
.bg-yellow {
  background-color: #E5BE3F;
}

/*@import "_boilerplate";*/
/*@import "_normalize";*/
html, body {
  font-family: "Poppins", sans-serif;
}

a {
  text-decoration: none;
}

button {
  border: none;
  background: none;
}

h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
  font-weight: bold;
  margin: 0.5em 0;
}

h1, .h1 {
  font-size: 4em;
}

h2, .h2 {
  font-size: 3em;
}

.swiper-pagination, .swiper-button-prev, .swiper-button-next {
  color: black !important;
}

.swiper-pagination-bullet-active {
  background-color: black !important;
}

.vertical-separator {
  width: 0.2em;
  height: 2em;
  background: white;
  margin: 0;
  border-radius: 1em;
}

.horizontal-separator {
  width: 5em;
  height: 0.5em;
  background-color: white;
  margin: 0.5em 0 2em;
  border-radius: 1em;
}

.content-container {
  margin-top: 15em;
  width: 100%;
  height: auto;
}

.action-btn {
  background: #E5BE3F;
  color: #FFFFFF;
  padding: 1em 2em;
  border-radius: 0.5em;
  transition: 0.2s ease-in-out background;
}
.action-btn:hover {
  background: #d4a91d;
}

#top-header {
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 15000;
}
#top-header .top-navbar {
  padding: 1.7em 8em;
  background: black;
  display: flex;
  flex-flow: row;
  background: rgba(0, 0, 0, 0.2);
}
#top-header .top-navbar .logo {
  font-weight: bold;
  font-size: 1.5em;
  letter-spacing: 2px;
  margin-right: auto;
  color: #E5BE3F;
  background-color: #FFFFFF;
  padding: 0.25em 0.5em;
  border-radius: 1em;
}
#top-header .top-navbar .top-navbar-inner {
  padding: 0;
  margin: 0;
  display: flex;
  flex-flow: row;
  align-items: center;
  list-style: none;
}
#top-header .top-navbar .top-navbar-inner li a {
  color: #FFFFFF;
}
#top-header .top-navbar .top-navbar-inner li:not(:last-of-type) {
  margin-right: 1em;
}

#hero {
  height: 100vh;
  width: 100%;
}
#hero .hero-container {
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: row;
  align-items: center;
}
#hero .hero-container .hero-text {
  padding: 0 4em;
  width: 100%;
  text-align: center;
}
@media (min-width: 768px) {
  #hero .hero-container .hero-text {
    width: 40%;
    text-align: left;
  }
}
#hero .hero-container .hero-text p {
  font-size: 1.2em;
}
#hero .hero-container .hero-img {
  display: none;
}
@media (min-width: 768px) {
  #hero .hero-container .hero-img {
    display: block;
    width: 60%;
    height: 100%;
    overflow: hidden;
  }
}
#hero .hero-container .hero-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

#who-we-are {
  background-color: black;
  padding: 5em;
  color: white;
}
#who-we-are h2, #who-we-are .h2, #who-we-are p {
  text-align: center;
}
#who-we-are p {
  font-size: 1.2em;
}

#branches {
  background: #E5BE3F;
  padding: 2.5em 0;
}
#branches header {
  text-align: center;
  margin-bottom: 0.5em;
}
#branches #branches-carousel {
  padding: 2em 3em 3em;
}
#branches #branches-carousel .branch-item {
  width: 100%;
  margin: auto;
  overflow: hidden;
  border-radius: 50px;
  background: linear-gradient(145deg, #f5cb43, #ceab39);
  box-shadow: 17px 17px 34px #c3a236, -17px -17px 34px #ffdb48;
}
#branches #branches-carousel .branch-item img {
  width: 100%;
  height: 20em;
  object-fit: cover;
}
#branches #branches-carousel .branch-item .branch-content {
  padding: 2em;
}

.c-form {
  display: flex;
  flex-flow: column wrap;
}
.c-form .c-form-input {
  padding: 0.5em;
  margin-bottom: 0.5em;
  border-radius: 0.5em;
  border: 3px solid #000000;
  height: 4em;
}
.c-form .c-form-input:focus {
  outline-width: 4px;
  outline-color: #E5BE3F;
}
.c-form .c-form-input-text-area {
  height: 10em;
}

#login-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

#loginForm {
  display: flex;
  flex-flow: column;
  margin-bottom: 7.5em;
}
#loginForm legend {
  padding: 0.25em 0.5em;
  border-radius: 1em;
  color: #E5BE3F;
  background-color: #FFFFFF;
  font-weight: bold;
  font-size: 1.5em;
  align-self: flex-start;
  width: auto;
}

.form-validation-msg {
  color: red;
  margin-top: 0.3em;
  display: block;
}

.footer {
  margin-top: auto;
  background: black;
  padding: 3em;
  color: white;
}
.footer .footer-info {
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  text-align: center;
}
@media (min-width: 992px) {
  .footer .footer-info {
    flex-flow: row wrap;
    justify-content: space-between;
    text-align: initial;
  }
}
.footer .footer-info .author {
  color: #E5BE3F;
}